'use client';

import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { useFirebase, signIn } from '@/lib/firebaseClient';
import { useRouter } from 'next/navigation';

const AdminSignIn: React.FC = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const { toast } = useToast();
  const { firebaseApp, auth } = useFirebase();
  const router = useRouter();

  const handleSignIn = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setIsLoading(true);

    if (!auth) {
      toast({
        title: 'Error',
        description: 'Firebase is not initialized. Please try again later.',
        variant: 'destructive',
      });
      setIsLoading(false);
      return;
    }

    try {
      const user = await signIn(auth, email, password);

      if (user) {
        // User signed in successfully.
        toast({
          title: 'Login Successful',
          description: 'Redirecting to admin dashboard...',
        });
        setTimeout(() => {
          router.push('/admin'); // Redirect to the admin page
        }, 1000);
      } else {
        toast({
          title: 'Login Failed',
          description: 'Invalid credentials or network error.',
          variant: 'destructive',
        });
      }

    } catch (error: any) {
      console.error('Error signing in:', error);
      toast({
        title: 'Login Failed',
        description: error.message || 'Invalid credentials or network error.',
        variant: 'destructive',
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="space-y-6">
      <form onSubmit={handleSignIn} className="space-y-4">
        <div className="space-y-2">
          <Label htmlFor="email">Email (Username)</Label>
          <Input
            type="email"
            id="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
            placeholder="admin@example.com"
            disabled={isLoading}
          />
        </div>
        <div className="space-y-2">
          <Label htmlFor="password">Password</Label>
          <Input
            type="password"
            id="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
            placeholder="••••••••"
            disabled={isLoading}
          />
        </div>
        <Button type="submit" className="w-full" disabled={isLoading}>
          {isLoading ? 'Signing In...' : 'Sign In'}
        </Button>
      </form>
      <p className="text-xs text-muted-foreground text-center">
        Note: Ensure Email/Password sign-in is enabled in your Firebase project and an admin user exists.
      </p>
    </div>
  );
};
AdminSignIn.displayName = 'AdminSignIn';

export default AdminSignIn;
